import React from "react";
import { Link } from 'react-router-dom';
import '../styles/card.css'

const Card = (props) => {

    const {book} = props
    const id = book.id

    return (   
    <div className="card-cont" id={'card-' + book.id}>
    <div className="cardimg-cont">
        <img className="card-img" src={book.img} alt="Card cap" />
    </div>
    <div className="card-body">
        <h4>{book.name}</h4>
        <p className="card-text"></p>
    </div>
    <Link
        to={{
                pathname: `${id}`,
                state: {...book}
                }}
    >
    <span className='card-link'></span>
    </Link>
    </div>
    )
}

export default Card