import React, { useEffect, useState } from "react";
import Card from './Card'
import '../styles/cardcarousel.css'

const CardCarousel = (props) => {

    const { books } = props;
    const [change, setChange] = useState(false)
    const id = "id" + Math.random().toString(16).slice(2)
    
    const leftBtn = () => {return setChange('left')}
    const rightBtn = () => {return setChange('right')}

    useEffect(() => {
        const scrollDiv = document.getElementById(id)
        if(scrollDiv.length > 0){
            console.log('exists')
        }
        if(change === 'left'){
            scrollDiv.scrollLeft -= 150
            console.log('left')
            setChange(false)
        }
        if(change === 'right'){
            scrollDiv.scrollLeft += 150
            console.log('right')
            setChange(false)
        }
    },[change])

    return (
        <div className='cardcar-cont'>
            <div>
                <button className='nav-btn' onClick={leftBtn}>
                    <span className='material-icons'>
                        arrow_back_ios
                    </span>
                </button>
                <button className='nav-btn right' onClick={rightBtn}>
                    <span className='material-icons'>
                        arrow_forward_ios
                    </span>
                </button>
                <div className='cards-cont' id={id}>
                    <div className='mid-cont'>
                        {books.map(book => {return <Card book={book}/>})}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CardCarousel